@import './imports/global';

.aui-header .aui-quicksearch {
    @unfocused-background-color: @ak-color-N90A;
    @focused-background-color: @ak-color-N90A;
    @unfocused-font-color: rgba(255, 255, 255, 0.6);
    @focused-font-color: @ak-color-B50;
    @border-radius: 3px;
    @height: 30px;
    @margin-top: 5px;
    @margin-bottom: 5px;
    @icon-height: 16px;
    @icon-width: 16px;
    @baseline-adjustment: 1px;
    @icon-margin-top: (@height + @margin-top + @margin-bottom - @icon-width) / 2 - @baseline-adjustment;
    @padding-left-right: 10px;
    @icon-right-indent: @icon-width;
    @text-padding-right: @icon-right-indent + @padding-left-right;

    position: relative;     //needs to be relative so we can have a absolutely positioned icon

    input[type='text'] {
        background: @unfocused-background-color;
        border-radius: @border-radius;
        box-shadow: none;
        color: @unfocused-font-color;
        height: @height;
        margin: @margin-top 0;
        padding-right: @text-padding-right;

        &[type='text']:focus {
            background: @focused-background-color;
            color: @focused-font-color;
        }


        /* Placeholder styling
         * - You have to one rule per vendor because user agents are required to ignore a rule with an unknown selector.
         *   Since WebKit doesn’t know the proprietary Mozilla selector and vice versa, you have to include them separately.
         *   See http://stackoverflow.com/questions/2610497/change-an-inputs-html5-placeholder-color-with-css#answer-2610741
         */

        &::-webkit-input-placeholder {
            color: @unfocused-font-color;
        }

        &::-moz-placeholder {  /* Firefox 19+ */
            color: @unfocused-font-color;
        }

        &:-ms-input-placeholder {
            color: @unfocused-font-color;
        }

        //focus placeholder

        &:focus {
            &::-webkit-input-placeholder {
                color: @focused-font-color;
            }

            &::-moz-placeholder {  /* Firefox 19+ */
                color: @focused-font-color;
            }

            &:-ms-input-placeholder {
                color: @focused-font-color;
            }
        }

    }

    //icon
    &:after {
        color: @unfocused-font-color;
        content: "\f18d"; // search icon
        font-family: "Atlassian Icons";
        height: @icon-height;
        margin-top: @icon-margin-top;
        pointer-events: none; // IE10+
        position: absolute;
        right: @icon-right-indent;
        top: 0;
        width: @icon-width;
    }
}



